'use client';

import { motion } from 'framer-motion';
import { useState, useEffect } from 'react';
import { getProjectStats } from '@/data/projects';

export default function HeroSection() {
  const [stats, setStats] = useState(getProjectStats());
  
  // Refresh stats whenever component renders - will be updated when project status changes
  useEffect(() => {
    setStats(getProjectStats());
  }, []);

  return (
    <section className="relative min-h-screen flex items-center justify-center overflow-hidden">
      {/* Background elements */}
      <div className="absolute inset-0 z-0">
        <div className="absolute inset-0 bg-gradient-to-br from-background-dark to-background-darker" />
        
        {/* Enhanced radial gradient */}
        <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_50%,rgba(59,130,246,0.15),transparent_50%)]" />
        
        {/* Background dots/grid pattern */}
        <div className="absolute inset-0 opacity-10">
          <div className="absolute inset-0" style={{ 
            backgroundImage: 'radial-gradient(#666 1px, transparent 1px)', 
            backgroundSize: '20px 20px' 
          }}></div>
        </div>
        
        {/* Accent glows */}
        <div className="absolute top-1/3 left-1/4 w-64 h-64 bg-primary opacity-10 rounded-full filter blur-3xl"></div>
        <div className="absolute bottom-1/3 right-1/4 w-64 h-64 bg-secondary opacity-10 rounded-full filter blur-3xl"></div>
      </div>

      {/* Content */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="relative z-10 text-center px-4 sm:px-6 lg:px-8"
      >
        <div className="bg-background-darker p-12 rounded-xl border border-gray-800 shadow-2xl max-w-3xl mx-auto">
          <motion.div
            initial={{ scale: 0.9, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ delay: 0.2, duration: 0.5 }}
            className="relative inline-block mb-6"
          >
            <div className="absolute -inset-1 bg-gradient-to-r from-primary to-secondary opacity-75 rounded-lg blur"></div>
            <h1 className="relative text-4xl sm:text-5xl lg:text-6xl font-bold text-text-primary px-4 py-2">
              My Journey to{' '}
              <span className="text-primary">LLM Specialist</span>
            </h1>
          </motion.div>

          <p className="text-lg sm:text-xl text-text-secondary mb-12 max-w-2xl mx-auto">
            Tracking my skills growth through projects and milestones.
          </p>

          {/* Progress Bar */}
          <div className="max-w-md mx-auto p-6 bg-background-dark rounded-lg border border-gray-800">
            <div className="flex justify-between text-sm text-text-secondary mb-2">
              <span>Progress</span>
              <span>{stats.completionPercentage}% Completed</span>
            </div>
            <div className="h-3 bg-background-darker rounded-full overflow-hidden border border-gray-800 shadow-inner">
              <motion.div
                initial={{ width: 0 }}
                animate={{ width: `${stats.completionPercentage}%` }}
                transition={{ duration: 1, delay: 0.5 }}
                className="h-full bg-primary rounded-full relative"
              >
                {/* Animated gradient overlay */}
                <div className="absolute inset-0 bg-gradient-to-r from-primary/50 to-primary animate-pulse"></div>
              </motion.div>
            </div>
            
            {/* Progress stats */}
            <div className="grid grid-cols-3 gap-4 mt-6 pt-4 border-t border-gray-800">
              <motion.div
                whileHover={{ scale: 1.05 }}
                className="text-center p-2 rounded-lg bg-success/10 border border-success/20 transition-all duration-300"
              >
                <div className="text-lg font-bold text-success">{stats.completed}</div>
                <div className="text-xs text-text-secondary">Completed</div>
              </motion.div>
              
              <motion.div
                whileHover={{ scale: 1.05 }}
                className="text-center p-2 rounded-lg bg-primary/10 border border-primary/20 transition-all duration-300"
              >
                <div className="text-lg font-bold text-primary">{stats.inProgress}</div>
                <div className="text-xs text-text-secondary">In Progress</div>
              </motion.div>
              
              <motion.div
                whileHover={{ scale: 1.05 }}
                className="text-center p-2 rounded-lg bg-gray-700/10 border border-gray-700/20 transition-all duration-300"
              >
                <div className="text-lg font-bold text-text-primary">{stats.notStarted}</div>
                <div className="text-xs text-text-secondary">Upcoming</div>
              </motion.div>
            </div>
          </div>
          
          {/* Action buttons */}
          <div className="mt-10 flex flex-wrap gap-4 justify-center">
            <motion.a
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              href="/roadmap"
              className="px-6 py-3 bg-primary hover:bg-primary-hover text-white font-medium rounded-lg shadow-lg shadow-primary/20 flex items-center gap-2 transition-colors"
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2">
                <path strokeLinecap="round" strokeLinejoin="round" d="M9 5l7 7-7 7" />
              </svg>
              View Roadmap
            </motion.a>
            
            <motion.a
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              href="/milestones"
              className="px-6 py-3 bg-background-dark hover:bg-gray-800 text-text-primary font-medium rounded-lg border border-gray-700 flex items-center gap-2 transition-colors"
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2">
                <path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" />
              </svg>
              Explore Milestones
            </motion.a>
          </div>
        </div>
      </motion.div>
    </section>
  );
} 