'use client';

import { motion } from 'framer-motion';
import Link from 'next/link';
import { useState } from 'react';
import { CheckIcon, XMarkIcon } from '@heroicons/react/24/outline';

export type Status = 'Completed' | 'In Progress' | 'Not Started';

interface CardProps {
  title: string;
  description: string;
  status: Status;
  githubLink?: string;
  detailsLink?: string;
  tags?: string[];
  onViewDetails?: () => void;
  month?: number;
  icon?: string;
  onStatusChange?: (newStatus: Status) => void;
}

const statusColors = {
  'Completed': 'bg-success border-success/30',
  'In Progress': 'bg-primary border-primary/30',
  'Not Started': 'bg-gray-600 border-gray-600/30',
};

export default function Card({
  title,
  description,
  status,
  githubLink,
  detailsLink,
  tags = [],
  onViewDetails,
  month,
  icon,
  onStatusChange,
}: CardProps) {
  const [isHovered, setIsHovered] = useState(false);

  const handleStatusToggle = () => {
    if (!onStatusChange) return;
    
    if (status === 'Completed') {
      onStatusChange('Not Started');
    } else if (status === 'In Progress') {
      onStatusChange('Completed');
    } else {
      onStatusChange('In Progress');
    }
  };

  return (
    <motion.div
      whileHover={{ y: -5 }}
      onHoverStart={() => setIsHovered(true)}
      onHoverEnd={() => setIsHovered(false)}
      className="bg-background-darker rounded-lg p-6 shadow-lg hover:shadow-primary/10 transition-all duration-300 border border-gray-800 h-full flex flex-col relative group"
    >
      {/* Status indicator strip */}
      <div className={`absolute top-0 left-0 w-1.5 h-full ${status === 'Completed' ? 'bg-success' : status === 'In Progress' ? 'bg-primary' : 'bg-gray-600'} rounded-l-lg`}></div>

      {/* Interactive status toggle button */}
      {onStatusChange && (
        <motion.button
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: isHovered ? 1 : 0, scale: isHovered ? 1 : 0.8 }}
          transition={{ duration: 0.2 }}
          onClick={handleStatusToggle}
          className={`absolute top-2 right-2 p-1.5 rounded-full ${status === 'Completed' ? 'bg-success/20 hover:bg-success/30' : status === 'In Progress' ? 'bg-primary/20 hover:bg-primary/30' : 'bg-gray-600/20 hover:bg-gray-600/30'} transition-colors z-10`}
          aria-label="Toggle status"
        >
          {status === 'Completed' ? (
            <CheckIcon className="h-4 w-4 text-success" />
          ) : status === 'In Progress' ? (
            <motion.div className="h-4 w-4 text-primary animate-pulse">‚è≥</motion.div>
          ) : (
            <XMarkIcon className="h-4 w-4 text-gray-400" />
          )}
        </motion.button>
      )}

      <div className="pl-3 flex flex-col h-full">
        <div className="flex justify-between items-start mb-4">
          <div className="flex items-center gap-2">
            {icon && <span className="text-2xl">{icon}</span>}
            <h3 className="text-xl font-semibold text-text-primary">{title}</h3>
          </div>
          <span className={`px-3 py-1 rounded-full text-sm ${statusColors[status]} text-white`}>
            {status}
          </span>
        </div>
        
        {month && (
          <div className="mb-3">
            <span className="text-xs text-primary font-medium">Month {month}</span>
          </div>
        )}
        
        <p className="text-text-secondary mb-4">{description}</p>
        
        {tags.length > 0 && (
          <div className="flex flex-wrap gap-2 mb-4">
            {tags.map((tag) => (
              <span
                key={tag}
                className="px-2 py-1 bg-background-dark rounded-md text-xs text-text-secondary"
              >
                {tag}
              </span>
            ))}
          </div>
        )}
        
        <div className="flex gap-3 mt-auto pt-4 border-t border-gray-800">
          {githubLink && (
            <Link
              href={githubLink}
              className="text-primary hover:text-primary-hover text-sm transition-colors flex items-center gap-1"
              target="_blank"
              rel="noopener noreferrer"
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2" className="w-4 h-4">
                <path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path>
              </svg>
              GitHub
            </Link>
          )}
          {detailsLink && onViewDetails && (
            <button
              onClick={onViewDetails}
              className="text-secondary hover:text-secondary-hover text-sm transition-colors flex items-center gap-1"
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2" className="w-4 h-4">
                <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                <path strokeLinecap="round" strokeLinejoin="round" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
              </svg>
              View Details
            </button>
          )}
          {detailsLink && !onViewDetails && (
            <Link
              href={detailsLink}
              className="text-secondary hover:text-secondary-hover text-sm transition-colors flex items-center gap-1"
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2" className="w-4 h-4">
                <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                <path strokeLinecap="round" strokeLinejoin="round" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
              </svg>
              View Details
            </Link>
          )}
        </div>
      </div>
    </motion.div>
  );
} 